import React, { useEffect, useState } from 'react';
import axios from 'axios';
import AddLLMEntryModal from './AddLLMEntryModal';
import EditLLMEntryModal from './EditLLMEntryModal';
import DeleteLLMEntryModal from './DeleteLLMEntryModal';
import loggingService from '../services/loggingService';
import { DndContext, closestCenter, KeyboardSensor, PointerSensor, useSensor, useSensors } from '@dnd-kit/core';
import { arrayMove, SortableContext, sortableKeyboardCoordinates, verticalListSortingStrategy } from '@dnd-kit/sortable';
import { SortableItem } from './SortableItem'; // Ensure SortableItem component is imported

function LLMEntriesTable() {
  const [entries, setEntries] = useState([]);
  const [isAddModalOpen, setIsAddModalOpen] = useState(false);
  const [isEditModalOpen, setIsEditModalOpen] = useState(false);
  const [isDeleteModalOpen, setIsDeleteModalOpen] = useState(false);
  const [selectedEntry, setSelectedEntry] = useState(null);

  useEffect(() => {
    fetchEntries();
  }, []);

  const fetchEntries = () => {
    axios.get(`${process.env.REACT_APP_API_URL}/llm/list`)
      .then(response => {
        setEntries(response.data);
      })
      .catch(error => {
        console.error('Error fetching LLM entries:', error.message);
        loggingService.logError({ message: 'Error fetching LLM entries', error: error.response ? error.response.data : error.message });
      });
  };

  const handleAddEntry = (entry) => {
    axios.post(`${process.env.REACT_APP_API_URL}/llm/add`, entry)
      .then(() => {
        fetchEntries();
        loggingService.logAction({ action: 'LLM entry added successfully' });
      })
      .catch(error => {
        console.error('Error adding LLM entry:', error.message);
        loggingService.logError({ message: 'Error adding LLM entry', error: error.response ? error.response.data : error.message });
      });
  };

  const handleUpdateEntry = (updatedEntry) => {
    axios.put(`${process.env.REACT_APP_API_URL}/llm/edit/${updatedEntry.id}`, updatedEntry)
      .then(() => {
        fetchEntries();
        loggingService.logAction({ action: 'LLM entry updated successfully' });
      })
      .catch(error => {
        console.error('Error updating LLM entry:', error.message);
        loggingService.logError({ message: 'Error updating LLM entry', error: error.response ? error.response.data : error.message });
      });
  };

  const handleDeleteEntry = (id) => {
    axios.delete(`${process.env.REACT_APP_API_URL}/llm/delete/${id}`)
      .then(() => {
        fetchEntries();
        loggingService.logAction({ action: 'LLM entry deleted successfully' });
      })
      .catch(error => {
        console.error('Error deleting LLM entry:', error.message);
        loggingService.logError({ message: 'Error deleting LLM entry', error: error.response ? error.response.data : error.message });
      });
  };

  const sensors = useSensors(
    useSensor(PointerSensor),
    useSensor(KeyboardSensor, {
      coordinateGetter: sortableKeyboardCoordinates,
    })
  );

  const handleDragEnd = (event) => {
    const { active, over } = event;

    if (active.id !== over.id) {
      const oldIndex = entries.findIndex(entry => entry.id === active.id);
      const newIndex = entries.findIndex(entry => entry.id === over.id);
      const updatedOrder = arrayMove([...entries], oldIndex, newIndex).map(entry => entry.id);

      axios.put(`${process.env.REACT_APP_API_URL}/llm/update-order`, { order: updatedOrder })
        .then(() => {
          loggingService.logAction({ action: 'LLM entries order updated successfully on the backend.' });
          setEntries(arrayMove(entries, oldIndex, newIndex));
        })
        .catch(error => {
          console.error('Error updating LLM entries order on the backend:', error.message);
          loggingService.logError({ message: 'Error updating LLM entries order on the backend', error: error.response ? error.response.data : error.message });
          // Revert to the original order in case of an error
          setEntries([...entries]);
        });
    }
  };

  return (
    <DndContext sensors={sensors} collisionDetection={closestCenter} onDragEnd={handleDragEnd}>
      <div className="container mx-auto p-4">
        <button onClick={() => setIsAddModalOpen(true)} className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded">Add LLM Entry</button>
        <SortableContext items={entries.map(entry => entry.id)} strategy={verticalListSortingStrategy}>
          <table className="min-w-full table-auto">
            <thead className="bg-gray-800 text-white">
              <tr>
                <th className="px-4 py-2">Model</th>
                <th className="px-4 py-2">Base URL</th>
                <th className="px-4 py-2">API Key</th>
                <th className="px-4 py-2">Label</th>
                <th className="px-4 py-2">Actions</th>
              </tr>
            </thead>
            <tbody>
              {entries.map((entry, index) => (
                <SortableItem key={entry.id} id={entry.id} index={index} entry={entry} setSelectedEntry={setSelectedEntry} setIsEditModalOpen={setIsEditModalOpen} setIsDeleteModalOpen={setIsDeleteModalOpen} />
              ))}
            </tbody>
          </table>
        </SortableContext>
        <AddLLMEntryModal isOpen={isAddModalOpen} onRequestClose={() => setIsAddModalOpen(false)} onAddEntry={handleAddEntry} refreshEntries={fetchEntries} />
        {selectedEntry && <EditLLMEntryModal isOpen={isEditModalOpen} onRequestClose={() => setIsEditModalOpen(false)} entry={selectedEntry} onUpdateEntry={handleUpdateEntry} refreshEntries={fetchEntries} />}
        {selectedEntry && <DeleteLLMEntryModal isOpen={isDeleteModalOpen} onRequestClose={() => setIsDeleteModalOpen(false)} entry={selectedEntry} onDeleteEntry={handleDeleteEntry} refreshEntries={fetchEntries} />}
      </div>
    </DndContext>
  );
}

export default LLMEntriesTable;